<script>
export default {
  data() {
    return {
      region: "",
      maker: "",
      year: "",
      model: "",
      subModel: "",
      series: "",
      code: "",
      name: "",
      priceMax: "",
      priceMin: "",
      areaMin: "",
      areaMax: "",
      sortBy: "",
      keys: ["Newest", "Oldest", "Waiting", "Reject"],
      headers: [
        { text: "Uploader", align: "start", value: "name" },
        { text: "Pattern Code", value: "code" },
        { text: "Class", value: "class" },
        { text: "Part", value: "part" },
        { text: "Upload Date", value: "date" },
        { text: "Price", value: "price" },
        { text: "Reject", value: "reject" },
        { text: "Details", value: "actions", sortable: false },
      ],
      dialog: false,
      dialogApproval: false,
      dateJoin: "",
      nameDialog: "",
      companyName: "",
      countryName: "",
      contactEmail: "",
      imgUrl: "",
      codeValue: "",
      newPattern: false,
      rejectPattern: false,
      dateStart: "",
      dateEnd: "",
      menuStart: false,
      menuEnd: false,
      titleDialogApproval: "",
      isApproval: false,
      isCancel: false,
      isReason: false,
      isReject: false,
    };
  },

  methods: {
    resetInput() {
      let inputCheckbox = document.querySelectorAll(
        ".table_head .v-input--selection-controls__input input"
      );

      inputCheckbox.forEach((element) => {
        if (element.checked) {
          element.click();
        }
      });

      this.region = "";
      this.maker = "";
      this.year = "";
      this.model = "";
      this.subModel = "";
      this.series = "";
      this.code = "";
      this.name = "";
      this.priceMax = "";
      this.priceMin = "";
      this.areaMin = "";
      this.areaMax = "";
      this.sortBy = "";
      this.dateStart = "";
      this.dateEnd = "";
    },
    showDialog(item) {
      this.dialog = true;

      this.dateJoin = item.date;
      this.nameDialog = item.name;
      this.companyName = item.company;
      this.countryName = item.country;
      this.contactEmail = item.email;
      this.imgUrl = item.image;
      this.codeValue = item.code;
      this.newPattern = item.class.new;
      this.rejectPattern = item.reject;
    },
    closeDialog() {
      this.dialog = false;
    },
    showDialogApproval(status) {
      if (status === "reject") {
        this.dialogApproval = true;
        this.titleDialogApproval =
          "Are you sure you want to withdraw the pattern approval rejection?";
        this.isApproval = false;
        this.isCancel = false;
        this.isReason = false;
        this.isReject = true;
      } else {
        this.dialogApproval = true;
        this.titleDialogApproval =
          "Are you sure you want to approve the pattern?";
        this.isApproval = false;
        this.isCancel = false;
        this.isReason = false;
        this.isReject = false;
      }
    },
    closeDialogApproval() {
      if (this.isReason === true) {
        this.titleDialogApproval = "Pattern approval request rejected";
        this.isReason = false;
      } else {
        this.dialogApproval = false;
      }
    },
    confirmApproval() {
      if (this.isReject === true) {
        this.titleDialogApproval =
          "The pattern approval rejection withdrawal has been completed";
        this.isReason = false;
        this.isApproval = true;
      } else {
        if (this.isCancel === false) {
          this.titleDialogApproval = "Pattern approval is complete";
          this.isReason = false;
        } else {
          this.titleDialogApproval = "Please enter a reason";
          this.isReason = true;
        }
        this.isApproval = true;
      }
    },
    cancelPattern() {
      this.dialogApproval = true;
      this.isApproval = false;
      this.isCancel = true;
      this.titleDialogApproval =
        "Are you sure you want to decline the pattern approval request?";
    },
  },
};
</script>

